@import url(https://fonts.googleapis.com/css2?family=Poppins:ital,wght@0,400;0,700;1,400;1,700&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Roboto&display=swap);
.sideBar-wrapp {
	width: 212px;
	height: 900px;
	border-right: 1px solid #dadada;
	display: flex;
	flex-flow: column nowrap;
	justify-content: left;
	padding: 0 0 25px 25px;
	box-sizing: border-box;
}

.sideBar-wrapp .logo-img {
	position: relative;
	width: 40px;
	height: 25px;
	left: 33px;
	top: 40px;
}

nav {
	display: flex;
	flex-flow: column nowrap;
	margin-top: 110px;
}
nav a {
	margin: 0 0 36px 0;
	display: flex;
	flex-flow: row nowrap;
	align-items: center;
	font-size: 16px;
	line-height: 24px;
	letter-spacing: 0.374px;
	text-decoration: none;
}

nav a > img {
	margin-right: 12px;
}

nav .selected {
	color: red;
	margin-left: 12px;
	font-weight: 600;
}

.profileNav {
	margin-top: auto;
	display: flex;
	flex-flow: row nowrap;
	align-items: center;
}

.profileNav img {
	width: 24px;
	height: 24px;
	margin-right: 10px;
}

.profileNav a {
	font-size: 18px;
	line-height: 41px;
	letter-spacing: 0.374px;
	text-decoration: none;
}

.homepage {
	width: 1228px;
	max-height: 900px;
	box-sizing: border-box;
	display: flex;
	flex-flow: column wrap;
	box-sizing: border-box;
}

.page-title {
	font-size: 20px;
	line-height: 30px;
	margin: 50px 0 20px 33px;
}

.acccounts {
	width: 537px;
	box-sizing: border-box;
	display: flex;
	flex-flow: column nowrap;
	padding: 0 27px 200px 33px;
	letter-spacing: 0.374px;
}

.account {
	width: 448px;
	box-sizing: border-box;
	background: #ffffff;
	box-shadow: 0px 0px 10px rgba(0, 0, 0, 0.15);
	border-radius: 20px;
	display: flex;
	flex-flow: row nowrap;
	align-items: center;
	padding: 20px;
	margin-bottom: 20px;
	text-decoration: none;
	color: #000000;
}

.account > span {
	margin-left: auto;
	font-weight: bold;
	font-size: 20px;
	line-height: 30px;
}

.currency-icon {
	border-radius: 50%;
	display: flex;
	flex-flow: row nowrap;
	align-items: center;
	justify-content: center;
	width: 28px;
	height: 28px;
	margin-right: 7px;
}

.currency-icon span {
	font-weight: 500;
	font-size: 20px;
	line-height: 30px;
	color: #ffffff;
}

.euro {
	background-color: #42507f;
}

.gbp {
	background-color: #427f7a;
}

.usd {
	background-color: #f5855c;
}

.krw {
	background-color: #ffac30;
}

.account p {
	font-weight: 500;
	font-size: 15px;
	line-height: 22px;
}

.acccounts .active {
	color: #ffffff;
	background: linear-gradient(180deg, #fd3a6c 0%, #f5855c 100%);
	box-shadow: 0px 0px 7px #ee4874;
	margin-left: 30px;
}

.account-wrapp {
	width: 691px;
	box-sizing: border-box;
	background-color: #ffffff;
	margin-top: 50px;
	padding: 30px 45px 34px 45px;
	border-radius: 40px 0px 0px 0px;
	display: flex;
	flex-flow: column nowrap;
}

.account-wrapp a {
	text-decoration: none;
	color: #262626;
}

.account-wrapp p {
	font-size: 18px;
	line-height: 27px;
	color: #b5b5b5;
	margin-bottom: 10px;
}

.balance-wrapp {
	display: flex;
	flex-flow: row nowrap;
	justify-content: space-between;
	align-items: center;
	margin-bottom: 18px;
}

.balance-wrapp h2 {
	font-size: 64px;
	line-height: 96px;
	letter-spacing: 0.374px;
	color: #262626;
}

.balance-wrapp > a {
	width: 135px;
	height: 70px;
	border: 1px solid #fd3a6c;
	box-sizing: border-box;
	border-radius: 20px;
	display: flex;
	flex-flow: row nowrap;
	justify-content: space-between;
	align-items: center;
	padding: 20px;
}

.balance-wrapp a > span {
	font-size: 14px;
	color: #fd3a6c;
}

.quick-transition-wrapp {
	display: flex;
	flex-flow: row nowrap;
	justify-content: flex-start;
	align-items: center;
	margin-bottom: 96px;
}
.quick-transition-wrapp a {
	margin-right: 20px;
}
.send-money {
	width: 110px;
	height: 140px;
	background: #ffffff;
	border: 1px solid #fd3a6c;
	box-sizing: border-box;
	border-radius: 30px;
	display: flex;
	flex-flow: column nowrap;
	align-items: center;
	justify-content: center;
	padding: 35px 30px 19px 30px;
}

.send-money img {
	width: 27px;
	height: 30px;
	margin-bottom: 18px;
}

.send-money span {
	font-size: 14px;
	line-height: 120%;
	text-align: center;
	letter-spacing: 0.374px;
	color: #fd3a6c;
}

.send-toContact {
	width: 110px;
	height: 140px;
	background: #f8f8f8;
	border-radius: 30px;
	box-sizing: border-box;
	display: flex;
	flex-flow: column nowrap;
	align-items: center;
	padding: 17px 0 17px 0;
}

.send-toContact img {
	width: 70px;
	height: 70px;
	margin-bottom: 16px;
}

.send-toContact span {
	font-size: 14px;
	line-height: 21px;
}

.account-info-section {
	border: 1px solid #b5b5b5;
	border-radius: 30px;
	display: flex;
	flex-flow: column nowrap;
	align-items: center;
	margin-bottom: 26px;
}

.account-info-section div:first-child {
	border-bottom: 1px solid #b5b5b5;
}

.account-info-section > div {
	width: 100%;
	box-sizing: border-box;
	display: flex;
	flex-flow: row nowrap;
	align-items: center;
	padding: 26px 32px 26px 27px;
}

.account-info-section img {
	width: 24px;
	height: 24px;
	margin-right: 8px;
}

.account-info-section span {
	font-size: 18px;
	line-height: 24px;
	color: #b5b5b5;
}

.account-info-section span:last-child {
	font-weight: 500;
	color: #262626;
	margin-left: auto;
}

.account-info-share {
	display: flex;
	flex-flow: row nowrap;
	align-items: center;
}

.account-info-share span {
	margin-left: 29px;
}

.account-info-share div {
	width: -webkit-max-content;
	width: max-content;
	height: 24px;
	margin-left: auto;
}

.account-info-share a {
	width: 24px;
	height: 24px;
	margin-left: 8px;
}

.account-info-share img {
	margin-right: 0;
	vertical-align: middle;
}

.request-wrapp {
	width: 537px;
	box-sizing: border-box;
	border: 1px solid #dadada;
	border-bottom: none;
	border-radius: 40px 0px 0px 0px;
	padding: 46px 46px 40px 34px;
}

.request-card {
	display: flex;
	flex-flow: row nowrap;
	align-items: center;
	margin-bottom: 35px;
}

.request-card:last-child {
	margin-bottom: 0;
}

.request-card img:first-child {
	margin-right: 16px;
	width: 32px;
	height: 32px;
}

.request-card img:last-child {
	width: 16px;
	height: 16px;
}

.request-card > a {
	margin-left: auto;
}

.request-card h2 {
	font-weight: bold;
	font-size: 20px;
	line-height: 30px;
	margin-bottom: 5px;
}

.request-card span {
	font-family: "Roboto", sans-serif;
	font-size: 14px;
	line-height: 16px;
	color: #797979;
}

.send-money-page {
	width: 1227px;
	box-sizing: border-box;
	display: flex;
	flex-flow: row nowrap;
}

.title {
	padding: 50px 0 30px 33px;
	display: flex;
	flex-flow: row nowrap;
	align-items: center;
}

.title h2 {
	font-size: 20px;
	line-height: 30px;
	letter-spacing: 0.374px;
	margin-left: 9px;
}

.send-money-form {
	width: 630px;
	display: flex;
	flex-flow: row nowrap;
	justify-content: center;
	align-items: center;
}

.send-money-form img {
	width: 15px;
	height: 17px;
}

.send-money-form span {
	font-weight: bold;
	font-size: 20px;
	line-height: 30px;
	letter-spacing: 0.374px;
	margin-left: 13px;
}

body {
    margin: 0;
    padding: 0;
}
p, h2 {
    margin: 0;
    padding: 0;
}

.app-wrapp {
	width: 1440px;
	margin: 0 auto;
	background: #E5E5E5;
    font-family: 'Poppins', sans-serif;
    display: flex;
    flex-flow: row nowrap;
}

